<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left" >
            <div id="divRequestionItemsData" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-4 float-left">
                <div class="heading-border">
                    <h5 class="h5">
                        <span>{{ $t('ListOfAssest') }}</span>
                    </h5>
                </div>
                <div class="row" >
                    <div class="col-12 mb-2 mt-2">
                        <div class="mr-2 d-inline-block">
                            <span class="legend-Progress colorboxsquare"></span>{{ $t('ContainerProducts') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legend-ovrdue colorboxsquare"></span>{{ $t('NotAvailable') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="text-danger font-weight-bold">*</span>{{ $t('MandatoryItems') }}
                        </div>
                    </div>
                </div>
                <div class="row"  >
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left listing">
                        <div class="table-responsive tableborder">
                            <table id="tblassestassignmentinfo" class="table table-bordered dt-responsive nowrap my-0">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="min-width:50px;" class="text-center"><span>{{ $t('hash') }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span>{{ $t('ItemType') }}</span>
                                        </th>
                                        <th style="min-width:50px;" class="text-center"><span>{{ $t('ItemCode') }}</span>
                                        </th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('ItemName') }}</span>
                                        </th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('LocationName')
                                        }}</span></th>
                                        <th style="min-width:285px;"><span> {{ $t('UniqueName') }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('TermsAndConditions')
                                        }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('AvailableItems')
                                        }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span> {{
                                            $t('PendingAvailableQuantity') }}</span></th>
                                        <th style="min-width:150px;" class="text-center"><span>{{ $t('RequestQuantity') }}
                                            </span></th>
                                        <th style="min-width:200px;" class="text-center"><span>{{ $t('Compartment')
                                        }}</span></th>
                                        <th style="min-width:100px;" class="text-center"><span>{{ $t('Action') }}</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <template v-for="(item, index) in AssetListData">

                                        <tr :key="index" class="parentmain insertedrow bg-gold showassetcataloguegroup"
                                            :class="clsColor">
                                            <td>
                                                <span v-if="item.AssetCatalogueGroup.length > 0"
                                                    class='togglearrow arrowwhitecollapse_roles fa fa-caret-right fa-2x showcatalog showassetcatalog'></span>
                                                <span v-else class='showcatalog hidden showassetcatalog'></span>
                                            </td>
                                            <td class="text-center">{{ item.AssetType }}</td>
                                            <td class="text-center">{{ item.ItemCode }}</td>
                                            <td class="text-center item-name">{{ item.AssetCatalogName }}</td>
                                            <td class="text-center">{{ item.LocationName }}</td>
                                            <td class="text-center">
                                                <input type="text" name="txtcsUniqueName"
                                                    :class="item.ParentUniqueId > 0 ? 'form-control txtcsUniqueName disabled' : 'form-control txtcsUniqueName'"
                                                    v-model="item.AssetUniqueName" />
                                                <template v-if="item.ParentUniqueId <= 0">
                                                    <div class="d-block py-2 text-center"> {{ $t('OR') }}</div>                                                   
                                                    <input type="text"
                                                        class="form-control txtBarcodesearch col-10 text-center float-left"
                                                        placeholder="Enter Barcode" />
                                                    <a href="javascript:;" class="float-left green ancsearchbrcode m-2 "
                                                        title="ClickHereToSearch">
                                                        <em
                                                            class="searchicon enable fa fa-search action-icon text-success"></em>
                                                    </a>
                                                </template>
                                            </td>
                                            <td class="text-center">

                                                <input v-if="item.IsEnableTerms == 1" type="checkbox" value="1"
                                                    disabled="disabled"
                                                    checked="checked" class="assertermscondition" />

                                                <span v-else>{{ $t('NA') }}</span>

                                            </td>
                                            <td class="text-center">{{ item.AvailableItems }}</td>
                                            <td class="text-center">{{ item.PendingAvailableQuantity }}</td>
                                            <td class="text-center">
                                                <input type="text" class="txtitems form-control txtAssetQuentity"
                                                    :class="clsQtyDisabled" v-on:keyup="RequestedQuantity()" :min="1" v-model="RequestQuantity" />
                                            </td>
                                            <td class="text-center">
                                                <select
                                                    v-if="item.ContainerList != null && item.ContainerList.Count > 0 && !(item.ContainerList.Text == '' || item.ContainerList.Text == null)"
                                                    class="disabled form-control" readonly="readonly">
                                                    <option>{{ item.ContainerList.Text }}</option>
                                                </select>
                                                <span v-else>{{ $t('NA') }}</span>
                                            </td>
                                            <td id="lastrow1" class="text-center">
                                                <a v-if="item.HasRelatedProducts" href="javascript:;" class="relatedproduct"
                                                    @click="relatedproduct(item)" title="Relateditem"><em
                                                        class="fa fa-sitemap text-primary" aria-hidden="true"></em></a>
                                                <a @click="deleteItem(item.AssetCatalogueId)" href="javascript:;"
                                                    title="Remove"><em class="fa fa-trash text-danger action-icon"></em></a>'
                                            </td>
                                        </tr>
                                        <tr v-for="(child, cindex) in item.AssetCatalogueGroup" :key="cindex"
                                            :class="clsColor">
                                            <td style="">
                                                <span id="cataloguegroup_id" class='hidden showcatalog'></span>
                                            </td>
                                            <td class="text-center">{{ child.AssetType }}</td>
                                            <td class="text-center">{{ child.ItemCode }}</td>
                                            <td class="text-center item-name">{{ child.AssetCatalogName }}</td>
                                            <td class="text-center">{{ child.LocationName }}</td>
                                            <td class="text-center">
                                                <input type="text" name="txtcsUniqueName"
                                                    class="form-control txtcsUniqueName" v-model="child.AssetUniqueName" />
                                                <template v-if="item.ParentUniqueId <= 0">
                                                    <div class="d-block py-2 text-center"> {{ $t('OR') }}</div>                                                   
                                                    <input type="text"
                                                        class="form-control txtBarcodesearch col-10 text-center float-left"
                                                        placeholder="Enter Barcode" />
                                                    <a href="javascript:;" class="float-left green ancsearchbrcode m-2 "
                                                        title="ClickHereToSearch">
                                                        <i
                                                            class="searchicon enable fa fa-search action-icon text-success"></i>
                                                    </a>
                                                </template>
                                            </td>
                                            <td class="text-center">
                                                <input v-if="item.IsEnableTerms == 1" type="checkbox" value="1"
                                                    disabled="disabled" checked="checked" class="assertermscondition" />
                                                <span v-else>{{ $t('NA') }}</span>
                                            </td>
                                            <td class="text-center">{{ child.AvailableItems }}</td>
                                            <td class="text-center">{{ child.PendingAvailableQuantity }}</td>
                                            <td class="text-center">
                                                <input type="text" :min="1" class="txtitems form-control txtAssetQuentity"
                                                    :class="clsQtyDisabled" v-on:keyup="RequestedQuantity()" v-model="RequestQuantity" />
                                            </td>
                                            <td class="text-center">
                                                <select class="disabled form-control" readonly="readonly"
                                                    v-if="child.ContainerList != null && child.ContainerList.Count > 0 && !(child.ContainerList.Text == '' || child.ContainerList.Text == null)">
                                                    <option>{{ child.ContainerList.Text }}</option>
                                                </select>
                                                <span v-else>{{ $t('NA') }} </span>
                                            </td>
                                            <td id="lastrow1" class="text-center">
                                                <a v-if="child.HasRelatedProducts" href="javascript:;"
                                                    class="relatedproduct" @click="relatedproduct(child)"
                                                    title="Relateditem"><em class="fa fa-sitemap text-primary"
                                                        aria-hidden="true"></em></a>
                                                <a href="javascript:;" title="Remove"><em
                                                        class="fa fa-trash text-danger action-icon"></em></a>'
                                            </td>
                                        </tr>
                                    </template>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <RelatedProrduct v-if="Isrelatedproduct" @CloseRelatedPopup="CloseRelatedPopup"
            :RelatedProductDaata="RelatedProductDaata" :AssetListData="AssetListData" @RelatedProduct="RelatedProduct"></RelatedProrduct>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
import RelatedProrduct from '../../components/Modal/RelatedProductPopup.vue'
export default {
    components: { RelatedProrduct },
    props: {
        ListData: {
            type: Array,
            required: true
        },
        issueTo: {
            type: Number,
            required: true

        }
    },
    data() {
        return {
            isLoading:false,
            AssetListData: [],
            clsColor: '',
            clsQtyDisabled: '',
            UserId: null,
            RequestQuantity: 1,
            AssestArray: [],
            Isrelatedproduct: false,
            RelatedProductDaata: []
        }
    },
    created: function () {         
        this.GetCompanyAssestCatalogs();
        this.UserId = this.GetUserInfo.ID;
    },
    methods: {
        RelatedProduct:function(ths){            
            this.AssetListData.push(ths[0]);            
        },
        GetCompanyAssestCatalogs: function () {
            this.isLoading=true;
            var url = `id=${this.ListData[0].AssetCatalogueId}&isView=1&barcode=&assetType=&assetUniqueId=${this.ListData[0].ParentUniqueId}&selfCheckout=no&reqFrom=assetassignment&isFor=search&isuniqueSearch=1&issueTo=${this.issueTo}&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${this.ListData[0].LocationId}&RFID=0&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${this.ListData[0].ParentUniqueId}&catalogueIds=${this.ListData[0].AssetCatalogueId}`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                this.AssetListData = response.data;
                this.GetDataFromAsset();
                this.AssetListData.forEach(ele => {
                    if (ele.IsContainer == 1) {
                        this.clsColor = "low-bar";
                    }
                    this.clsQtyDisabled = ele.CreateUniqueNameListing && ele.ParentUniqueId > 0 ? "disabled" : "";
                    ele.AssetCatalogueGroup.forEach(child => {
                        if (child.CreateUniqueNameListing == true) {
                            child.AvailableItems = 1;
                            child.AsseetGroupQuantity = 1;
                        }
                        if (child.IsContainer == 1) {
                            this.clsColor = "low-bar";
                        }
                    });
                });
                this.isLoading=false;
            });
        },
        RequestedQuantity(){          
            this.GetDataFromAsset();
        },
        GetDataFromAsset() {
            var vm = this;
               vm.AssetListData.forEach(item => {
                vm.AssestArray=[];
                let obj = {
                    AssetCatalogUniqueId: item.ParentUniqueId,
                    RequestFromRequsition: 1,
                    AssetQuantity: item.AvailableItems,
                    RequestQuantity: parseInt(vm.RequestQuantity),
                    BatchId: '',
                    AssetUniqueName: item.Name,
                    IsGroup: null,
                    ParentAssetCatalogUniqueId: item.AssetCatalogueId
                }
                vm.AssestArray.push(obj);
            });

            vm.$emit('AssestArray', this.AssestArray);
        },


        relatedproduct: function (ths) {
    
            this.Isrelatedproduct = true;
            this.RelatedProductDaata.push(ths);
        },
        CloseRelatedPopup: function () {
            this.Isrelatedproduct = false;
        },

        deleteItem: function (value) {
           
            var vm = this;

            vm.confirmR(vm.$t('Please verify that you want to delete this record'), true, false, function (result) {
                const index = vm.AssetListData.findIndex(x => x.AssetCatalogueId === value);
                if (index !== -1) {
          
                    vm.AssetListData.splice(index, 1);
                    vm.$parent.IsAssetOfList=false
                }
           
              
                

                return vm.ShowAlert(
                    vm.$t("Product has been deleted Successfully "),
                    "success",
                    vm.$t("Alert")
                   
                );
                
            

            });
         
          
           
        }

    }
}
</script>