<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('RelatedProduct') }}
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>
                    </div>
                    <div class="border p-3">
                        <div class="row">
                            <div class="col-lg-12">
                                <div class="listing">
                                    <div class="table-responsive table-fix-header">
                                        <table id="tblRelatedDataItems"
                                            class="table table-bordered dt-responsive nowrap my-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th><span style="max-width: 7px;">#</span></th>
                                                    <th><span>{{ $t('AssetId') }}</span></th>
                                                    <th style="min-width: 300px;"><span>{{ $t('AvailableItems') }}</span>
                                                    </th>
                                                    <th><span>{{ $t('Required Quantity') }}</span></th>
                                                </tr>
                                            </thead>
                                            <tbody>

                                                <tr v-for="(item, index) in GetRelatedProductData" :key="index">
                                                    <td>
                                                        <div class="custom-control custom-checkbox">
                                                            <input ref="theCheckbox" :for='"chk_" + index'
                                                                :data-catalogid="item.ID" :data-parentid="item.ID"
                                                                type="checkbox" :value="item.ID" :id='"chk_" + index'
                                                                class="custom-control-input" @click="handleClick">
                                                            <label
                                                                class="custom-control-label universal-custom-control-label pt-1"
                                                                :for='"chk_" + index'></label>
                                                        </div>
                                                    </td>
                                                    <td :data-catalogid="item.ID"
                                                        :data-itemsavailable="item.AVAILIABLE_ITEM" :data-parentid="item.ID"
                                                        :data-id="item.ID" :data-itemname="item.NAME">{{ item.NAME }}</td>
                                                    <td :data-catalogid="item.ID"
                                                        :data-itemsavailable="item.AVAILIABLE_ITEM" :data-parentid="item.ID"
                                                        :data-id="item.ID" :data-itemname="item.NAME">
                                                        {{ item.AVAILIABLE_ITEM }}</td>
                                                    <td> <input class="form-control align-middle txtAssetQuentity"
                                                            type="text" v-model="txtAssetQuentity" name="record"></td>
                                                </tr>
                                                <tr>
                                                     <td  v-if="GetRelatedProductData.length==0" colspan="7" class="text-danger text-center no-record">No Record Found</td>
                                                 </tr>




                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-danger" @click="ClosePopup">{{ $t('Close')
                                    }}</button>
                                    <button type="button" id="relatedprdct" class="btn btn-success" @click="SaveChanges">{{
                                        $t('SaveChanges') }}</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../service/DataService';
export default {
    props: {
        RelatedProductDaata: {
            type: Array,
            required: true
        },
        AssetListData: {
            type: Array,
            required: true
        },
        GetRelatedProduct: {
            type: Array
        },
        ItemIndex:{
            type:Number
        }

    },
    data() {
        return {
            GetRelatedProductData: [],
            IsChecked: false,
            txtAssetQuentity: 1,
            RelatedProduct: []
        }
    },
    created: function () {
        debugger
        var vm = this;

        
        if (vm.GetRelatedProduct != null && vm.GetRelatedProduct.length > 0) {
            return vm.GetRelatedProductData = vm.GetRelatedProduct;
        }
        else
        {
            vm.FetchData();
        }



    },
    methods: {

        FetchData: function () {
            var url = `catId=${this.RelatedProductDaata[0].AssetCatalogueId}&type=ProductName`;
            DataService.GetRelatedProduct(url).then(response => {
                this.GetRelatedProductData = JSON.parse(JSON.parse(response.data.result).RESULT);
            })
        },
        SaveChanges: function () {
            var vm = this;
            if (vm.GetRelatedProduct != null && vm.GetRelatedProduct.length > 0) {
                vm.$parent.UpdateProductList(vm.txtAssetQuentity,vm.ItemIndex);
            }
            else
            {
                var locationId = vm.RelatedProductDaata[0].LocationId;
            var selectedProductArray = [];
            vm.AssetListData.forEach((item) => {
                selectedProductArray.push({ catalogueId: item.AssetCatalogueId, uniqueId: item.ParentUniqueId })
            });
            vm.GetRelatedProductData.forEach((item, index) => {
                if (vm.IsChecked) {
                    var relationType = item.ID == '0' ? 'Product' : 'ProductName';
                    var id = relationType == 'ProductName' ? item.ID : vm.RelatedProductDaata[0].ParentUniqueId;
                    var isMandatory = item.Mandatory;
                    var relatedCatalogueId = vm.RelatedProductDaata[0].AssetCatalogueId;
                    var relatedUniqueId = vm.RelatedProductDaata[0].ParentUniqueId;
                    var AssetQuentity = vm.txtAssetQuentity;
                    if (selectedProductArray.some(x => x.catalogueId == vm.RelatedProductDaata[0].AssetCatalogueId && vm.RelatedProductDaata[0].ParentUniqueId)) {
                        var catalogueIds = relationType == 'ProductName' ? id : null;
                        var uniqueIds = relationType == 'Product' ? id : null;
                        var assignedFrom = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignment);
                        var assignedTill = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignmentTill);
                        var IssueTo = vm.$parent.$parent.$parent.$parent.Users == undefined ? this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.Users) : 0;
                        var startTime = this.$options.filters.formatTime(assignedFrom);
                        var endTime = this.$options.filters.formatTime(assignedTill);
                        var url = `id=&isView=1&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=assetassignment&isFor=&isuniqueSearch=&issueTo=${IssueTo}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${locationId}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${uniqueIds}&catalogueIds=${catalogueIds}`;
                        DataService.GetCompanyAssestCatalogsDetalis(url).then((response) => {
                            vm.RelatedProduct = response.data;
                            vm.$emit('RelatedProduct', vm.RelatedProduct);
                            vm.ClosePopup();
                        });
                    }
                }
                else {
                    vm.$parent.Isrelatedproduct = false;
                }
            });
            }
            


        },
        ClosePopup: function () {
            this.$parent.CloseRelatedPopup();
        },
        handleClick() {
            if (this.$refs.theCheckbox[0].checked) {
                this.IsChecked = true;
            } else {
                this.IsChecked = false;
            }
        },
    }
}
</script>