                            <tr v-for="(child, cindex) in item.AssetCatalogueGroup" :key="cindex" :data-id="item.AssetCatalogueId" style="display:none;" class="insertedrowchild parentmid" data-isgroup="0" :data-assetcatalogid="child.AssetCatalogueId">
