                                    <div class="form-group custom-control custom-checkbox" v-if="child.IsEnableTerms == 1" @click="handleClick()">
                                        <a href="javascript:;" :data-path="item.AttachmentPath" :data-name="item.AttachmentName">
                                            <input type="checkbox" ref="theCheckbox" class="assertermscondition custom-control-input bb" :id="item.AssetCatalogueId" :name='"example" + index' :for='"chk_" + index' v-model="chkvalue" @click="handleClick()">
                                            <label class="custom-control-label universal-custom-control-label mt-1" :for='"chk_" + index' :name='"example" + index'>
                                                {{ $t('AgreeToTermsConditions') }}
                                                <a v-if="!child.IsEnableTerms" href="javascript:;" class="downloadtermfile ml-1" :data-path="item.AttachmentPath" :data-name="item.AttachmentName" title="ClickToView">{{
                                                            $t('ClickToView') }}</a>
                                            </label>
